import { useFormik } from "formik";
import Joi from "joi"
const Form = () => {
  const form = useFormik({
    initialValues: {
      name: "",
      email: "",
      telephon: "",
      message: "",
    },
    onSubmit(values) {
      console.log(values);
    },
    validate(values){
      const schema = Joi.object({
        name: Joi.string().min(2).max(255).required(),
        email: Joi.string().max(255).email({tlds:{allow:false}}).required(),
        telephon: Joi.number().min(7).required(),
        message: Joi.string().min(2).max(500).required()
      })
      const {error} = schema.validate(values, {abortEarly:false})
      let {errors} = {}
      if(!error){
        return null
      }

      for (const detail of error.details) {
        errors = {...errors, [detail.path]:detail.message}
        console.log(errors);
      }
      
      return errors
    }
  });
  return (
    <form onSubmit={form.handleSubmit} className="mt-3 p-3 rounded-3 bg-light">
      <h3 className="black-letter">How can I help you ?</h3>

      <div className="mb-3">
        <input
          {...form.getFieldProps("name")}
          type="text"
          name="name"
          className="form-control"
          id="user-name"
          placeholder="Name"
        />
        {form.touched.name && <span className="text-danger"> {form.errors.name} </span>}
      </div>
      <div className="mb-3">
        <input
          {...form.getFieldProps("email")}
          type="email"
          name="email"
          className="form-control"
          id="user-email"
          placeholder="Email"
        />
        {form.touched.email && <span className="text-danger"> {form.errors.email} </span>}
      </div>
      <div className="mb-3">
        <input
          {...form.getFieldProps("telephon")}
          type="number"
          name="telephon"
          className="form-control"
          id="user-telephon"
          placeholder="Telephon"
        />
        {form.touched.telephon && <span className="text-danger"> {form.errors.telephon} </span>}
      </div>
      <div className="mb-3">
        <input
          {...form.getFieldProps("message")}
          type="text"
          name="message"
          style={{ height: "150px" }}
          className="form-control"
          id="exampleInputPassword1"
          placeholder="Your message"
        />
        {form.touched.message && <span className="text-danger"> {form.errors.message} </span>}
      </div>

      <button type="submit" className="btn btn-outline-success">
        Submit
      </button>
    </form>
  );
};

export default Form;
