import sunImg from "../imgs/sun-regular.png";

const NavBar = () => {
  return (
    
      <nav className="navbar navbar-expand-lg bg-dark">
        <div className="container-fluid">
          <button
            className="navbar-toggler bg-success"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <a
                  className="nav-link header-Footer-Color"
                  href="#about-me-row"
                >
                  About Me
                </a>
              </li>
              <li className="nav-item">
                <a className="nav-link header-Footer-Color" href="#gallery">
                  Gallery
                </a>
              </li>

              <li className="nav-item dropdown">
                <a
                  className="nav-link dropdown-toggle header-Footer-Color"
                  href="..."
                  role="button"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                  rel="noopener noreferrer"
                >
                  Projects
                </a>
                <ul className="dropdown-menu bg-dark">
                  <li>
                    <a
                      className="dropdown-item header-Footer-Color"
                      href="https://vitorreisler.github.io/js_portfolio/motivational_watch/"
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      Motivation Watch
                    </a>
                  </li>
                  <li>
                    <a
                      className="dropdown-item header-Footer-Color"
                      href="https://vitorreisler.github.io/js_portfolio/tic_tac_toe_TS/src/index2.html"
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      Tic Tac Toe
                    </a>
                  </li>
                  <li>
                    <a
                      className="dropdown-item header-Footer-Color"
                      href="https://vitorreisler.github.io/js_portfolio/pokedex_ts/src/index.html"
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      Pokedex
                    </a>
                  </li>
                  <li>
                    <a
                      className="dropdown-item header-Footer-Color"
                      href="https://vitorreisler.github.io/js_portfolio/drums_ts/src/index.html"
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      Drum Sounds
                    </a>
                  </li>
                  <li>
                    <a
                      className="dropdown-item header-Footer-Color"
                      href="https://vitorreisler.github.io/js_portfolio/work_hours/index.html"
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      Salary Calculator
                    </a>
                  </li>
                  <li>
                    <a
                      className="dropdown-item header-Footer-Color"
                      href="https://vitorreisler.github.io/js_portfolio/dictionary/index.html"
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      API Dictionary
                    </a>
                  </li>
                </ul>
              </li>
            </ul>

            <div id="social">
              <ul className="nav justify-content-center bg-dark">
                <li className="nav-item">
                  <img id="darkLight-icon" src={sunImg} alt="" />
                </li>

                <li className="nav-item">
                  <a
                    href="#gallery"
                    className="nav-link px-2 header-Footer-Color"
                    rel="noopener noreferrer"
                  >
                    <i
                      className="bi-solid bi-folder"
                      style={{ color: "#198754" }}
                    ></i>
                  </a>
                </li>

                <li className="nav-item">
                  <a
                    href="https://github.com/vitorreisler"
                    className="nav-link px-2 header-Footer-Color"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    <i
                      className="bi-brands bi-lg bi-github"
                      style={{ color: "#198654" }}
                    ></i>
                  </a>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </nav>
   
  );
};

export default NavBar;
