import vitorImg from "../imgs/vitor-perfil-3x4.jpg"


import CV from "../CV/CV_VITOR_Linkedin.pdf"

const AboutMe = ({htmlImg,cssImg,sassImg,bootstrapImg,jsImg,tsImg,apiImg,oopImg}) => {
  return (
    <div className="container text-center bg-light rounded-4 mt-3">
      <div id="about-me-row" className="row about-me-row">
        <div className="col col-img-about-me">
          <img
            className="roll-in-left"
            id="about-me-img"
            src={vitorImg}
            alt="Vitor"
          />
          <a
            className="btn btn-outline-success btn-cv d-block w-50 mt-3"
            href={CV}
            download={CV}
            target="_self"
            role="button"
          >
            Download
          </a>
        </div>
        <div className="col">
          <p className="black-letter">
            Hello, I'm Vitor, a passionate web developer with a focus on
            creating immersive digital experiences. I have a solid foundation in
            HTML5 and CSS3, which allows me to craft visually appealing and
            responsive websites. Currently, I'm diving deep into the world of
            JavaScript and TypeScript, as I strive to become a proficient
            Fullstack Developer in the near future. I'm excited about the
            endless possibilities that lie ahead and am committed to continuous
            learning to achieve my goal of becoming a well-rounded and skilled
            programmer.
          </p>
          <div id="technologys-img-container">
            <img
              src={htmlImg}
              alt="html-5-logo"
              className="technology-img scale-up-center rotate-center"
            />
            <img
              src={cssImg}
              alt="css3-logo"
              className="technology-img scale-up-center rotate-center"
            />
            <img
              src={sassImg}
              alt="Sass-Logo"
              className="technology-img scale-up-center rotate-center"
            />
            <img
              src={bootstrapImg}
              alt="Bootstrap-logo"
              className="technology-img scale-up-center rotate-center"
            />
            <img
              src={jsImg}
              alt="js-logo"
              className="technology-img scale-up-center rotate-center"
            />
            <img
              src={tsImg}
              alt="TS-logo"
              className="technology-img scale-up-center rotate-center"
            />
            <img
              src={apiImg}
              alt="API-logo"
              className="technology-img scale-up-center rotate-center"
            />
            <img
              src={oopImg}
              alt="oop-logo"
              className="technology-img scale-up-center rotate-center"
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default AboutMe;
