const Footer = () => {
  return (
    <>
      <div className="container pt-4">
        <section className="mb-4">

          <a
            className="btn btn-link btn-floating btn-lg text-dark m-1 scale"
            href="https://instagram.com/vi_gr"
            target="_blank"
            rel="noopener noreferrer"
            role="button"
            data-mdb-ripple-color="dark"
          >
            <i className="bi bi-instagram footer-i"></i>
          </a>

          <a
            className="btn btn-link btn-floating btn-lg text-dark m-1 scale"
            href="https://www.linkedin.com/in/vitorreisler/"
            target="_blank"
            rel="noopener noreferrer"
            role="button"
            data-mdb-ripple-color="dark"
          >
            <i className="bi bi-linkedin footer-i"></i>
          </a>

          <a
            className="btn btn-link btn-floating btn-lg text-dark m-1 scale"
            href="https://github.com/vitorreisler"
            target="_blank"
            rel="noopener noreferrer"
            role="button"
            data-mdb-ripple-color="dark"
          >
            <i className="bi bi-github footer-i"></i>
          </a>
        </section>
      </div>

      <div
        className="text-center text-dark p-3"
        style={{ backgroundColor: "rgba(0, 0, 0, 0.2);" }}
      >
        © {new Date().getFullYear()} Copyright:
        <a className="text-dark" href="https://github.com/vitorreisler">
          Vitor Reisler
        </a>
      </div>
    </>
  );
};

export default Footer;
