import motivationalWatchImg from "../imgs/motivationalWatch.png";
import ticTacToeImg from "../imgs/tic-tac-toe.png";
import pokedexImg from "../imgs/pokedex.png";
import drumsImg from "../imgs/drumsSounds.png";
import salaryCalcImg from "../imgs/workedHours.png";
import apiDictionaryImg from "../imgs/api-dictionary.png";
import dictionaryDownload from "../projectDownloads/dictionary.zip";
import drumsDownload from "../projectDownloads/drums_ts.zip";
import motivationalWatchDownload from "../projectDownloads/motivational_watch.zip";
import pokedexDownload from "../projectDownloads/pokedex_ts.zip";
import ticTacToeDownload from "../projectDownloads/tic_tac_toe_TS.zip";
import workHoursDownload from "../projectDownloads/work_hours.zip";
const Gallery = ({
  htmlImg,
  cssImg,
  sassImg,
  bootstrapImg,
  jsImg,
  tsImg,
  apiImg,
  oopImg,
}) => {
  return (
    <div id="gallery" className="row row-cols-1 row-cols-md-2 g-4 mt-2">
      <div className="col">
        <div className="card bg-light">
          <img
            src={motivationalWatchImg}
            className="card-img-top"
            alt="motivational Watch"
          />
          <div className="card-body">
            <h5 className="card-title">Motivational Watch</h5>
            <p className="card-text black-letter">
              If you having bad days, just want to be a little bit more
              motivate, or want to know how is the weather in your next city
              vacation ? <br />
              This app will work well for you, enjoy
            </p>
            <div className="card-body-technologys">
              <h6>Used Technologys:</h6>
              <img className="technology-img" src={htmlImg} alt="html5-logo" />
              <img className="technology-img" src={cssImg} alt="css3-logo" />
              <img
                className="technology-img"
                src={bootstrapImg}
                alt="Bootstrap-logo"
              />
              <img className="technology-img" src={jsImg} alt="js-logo" />
              <img className="technology-img" src={tsImg} alt="TS-logo" />
              <img
                className="technology-img"
                src={apiImg}
                alt="api-logo.png-logo"
              />
              <div className="card-button-download-container">
                <a
                  className="btn btn-outline-success d-block w-25 mt-3"
                  href="https://vitorreisler.github.io/js_portfolio/motivational_watch/"
                  target="_blank"
                  rel="noopener noreferrer"
                  role="button"
                >
                  More
                </a>
                <a
                  className="btn btn-outline-success d-block w-50 mt-3"
                  href={motivationalWatchDownload}
                  download={motivationalWatchDownload}
                  target="_self"
                  role="button"
                >
                  Download
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="col">
        <div className="card bg-light">
          <img src={ticTacToeImg} className="card-img-top" alt="tic-tac-toe" />
          <div className="card-body">
            <h5 className="card-title">Tic Tac Toe Game</h5>
            <p className="card-text black-letter">
              In this project, I've created a classic game of Tic Tac Toe using
              TypeScript. The game features a user-friendly interface where
              players can take turns marking Xs and Os on a 3x3 grid. With
              TypeScript, I've enhanced the codebase with static typing, making
              it more robust and maintainable. Players can compete against each
              other in a friendly match, and the game dynamically updates the
              board and checks for a winner after each move.
            </p>
            <h6>Used Technologys:</h6>
            <img className="technology-img" src={htmlImg} alt="html-5-logo" />
            <img className="technology-img" src={cssImg} alt="css3-logo" />
            <img className="technology-img" src={tsImg} alt="TS-logo" />
            <img className="technology-img" src={jsImg} alt="js-logo" />
            <div className="card-button-download-container">
              <a
                className="btn btn-outline-success d-block w-25 mt-3"
                href="https://vitorreisler.github.io/js_portfolio/tic_tac_toe_TS/src/index2.html"
                target="_blank"
                rel="noopener noreferrer"
                role="button"
              >
                More
              </a>
              <a
                className="btn btn-outline-success d-block w-50 mt-3"
                href={ticTacToeDownload}
                download={ticTacToeDownload}
                target="_self"
                role="button"
              >
                Download
              </a>
            </div>
          </div>
        </div>
      </div>

      <div className="col">
        <div className="card bg-light">
          <img src={pokedexImg} className="card-img-top" alt="pokedex" />
          <div className="card-body">
            <h5 className="card-title">PokeDex</h5>
            <p className="card-text black-letter">
              In this project, I've developed an interactive Pokédex application
              using TypeScript and integrating with an API to fetch Pokémon
              data. The Pokédex features a user-friendly interface where users
              can search for Pokémon by name, and the application dynamically
              displays detailed information about each Pokémon, including their
              names and abilities. The integration with the API allows the
              application to fetch real-time data, making it up-to-date with the
              latest Pokémon information.
            </p>
            <h6>Used Technologys:</h6>
            <img className="technology-img" src={htmlImg} alt="html-5-logo" />
            <img className="technology-img" src={cssImg} alt="css3-logo" />
            <img
              className="technology-img"
              src={bootstrapImg}
              alt="Bootstrap-logo"
            />
            <img className="technology-img" src={jsImg} alt="js-logo" />
            <img className="technology-img" src={tsImg} alt="TS-logo" />
            <img className="technology-img" src={apiImg} alt="api-logo" />
            <div className="card-button-download-container">
              <a
                className="btn btn-outline-success d-block w-25 mt-3"
                href="https://vitorreisler.github.io/js_portfolio/pokedex_ts/src/index.html"
                target="_blank"
                rel="noopener noreferrer"
                role="button"
              >
                More
              </a>
              <a
                className="btn btn-outline-success d-block w-50 mt-3"
                href={pokedexDownload}
                download={pokedexDownload}
                target="_self"
                role="button"
              >
                Download
              </a>
            </div>
          </div>
        </div>
      </div>

      <div className="col">
        <div className="card bg-light">
          <img src={drumsImg} className="card-img-top" alt="..." />
          <div className="card-body">
            <h5 className="card-title">Drums Sounds</h5>
            <p className="card-text black-letter">
              In this project, I've created an interactive drum kit using HTML,
              CSS, and JavaScript. The drum kit provides a fun and engaging
              experience where users can play different drum sounds by pressing
              the corresponding keys on their keyboard or by clicking on the
              on-screen buttons. The user-friendly interface and responsive
              design allow the drum kit to work seamlessly across various
              devices. JavaScript powers the interactivity of the drum kit,
              mapping keyboard events to trigger the appropriate drum sounds.
            </p>
            <h6>Used Technologys:</h6>
            <img className="technology-img" src={htmlImg} alt="html-5-logo" />
            <img className="technology-img" src={cssImg} alt="css3-logo" />
            <img className="technology-img" src={jsImg} alt="js-logo" />
            <div className="card-button-download-container">
              <a
                className="btn btn-outline-success d-block w-25 mt-3"
                href="https://vitorreisler.github.io/js_portfolio/drums_ts/src/index.html"
                target="_blank"
                rel="noopener noreferrer"
                role="button"
              >
                More
              </a>
              <a
                className="btn btn-outline-success d-block w-50 mt-3"
                href={drumsDownload}
                download={drumsDownload}
                target="_self"
                role="button"
              >
                Download
              </a>
            </div>
          </div>
        </div>
      </div>

      <div className="col">
        <div className="card bg-light">
          <img
            src={salaryCalcImg}
            className="card-img-top"
            alt="Worked Hours Project"
          />
          <div className="card-body">
            <h5 className="card-title">Salary Calculator</h5>
            <p className="card-text black-letter">
              In this project, I've developed a salary calculator using
              Object-Oriented Programming (OOP) principles, along with HTML,
              CSS, and JavaScript. The calculator provides a practical tool for
              users to calculate their earnings based on the number of days
              worked, hours worked per day, and the hourly wage. Additionally,
              users can input the number of hours worked as overtime at 125% and
              150% rates.
            </p>
            <h6>Used Technologys:</h6>
            <img className="technology-img" src={htmlImg} alt="html-5-logo" />
            <img className="technology-img" src={cssImg} alt="css3-logo" />
            <img className="technology-img" src={jsImg} alt="js-logo" />
            <img className="technology-img" src={oopImg} alt="oop-logo" />
            <div className="card-button-download-container">
              <a
                className="btn btn-outline-success d-block w-25 mt-3"
                href="https://vitorreisler.github.io/js_portfolio/work_hours/index.html"
                target="_blank"
                rel="noopener noreferrer"
                role="button"
              >
                More
              </a>
              <a
                className="btn btn-outline-success d-block w-50 mt-3"
                href={workHoursDownload}
                download={workHoursDownload}
                target="_self"
                role="button"
              >
                Download
              </a>
            </div>
          </div>
        </div>
      </div>

      <div className="col">
        <div className="card bg-light">
          <img
            src={apiDictionaryImg}
            className="card-img-top"
            alt="API Dictionary project"
          />
          <div className="card-body">
            <h5 className="card-title">API Dictionary</h5>
            <p className="card-text black-letter">
              In this project, I've developed a dictionary application using
              Sass, TypeScript, and API integration. The dictionary provides a
              user-friendly interface where users can search for word
              definitions and meanings. The application dynamically fetches data
              from an external API, ensuring that users receive up-to-date and
              accurate information.
            </p>
            <h6>Used Technologys:</h6>
            <img className="technology-img" src={htmlImg} alt="html-5-logo" />
            <img className="technology-img" src={cssImg} alt="css3-logo" />
            <img className="technology-img" src={sassImg} alt="Sass-Logo" />
            <img className="technology-img" src={jsImg} alt="js-logo" />
            <img className="technology-img" src={tsImg} alt="TS-logo" />
            <img className="technology-img" src={apiImg} alt="API-logo" />
            <div className="card-button-download-container">
              <a
                className="btn btn-outline-success d-block w-25 mt-3"
                href="https://vitorreisler.github.io/js_portfolio/dictionary/index.html"
                role="button"
              >
                More
              </a>
              <a
                className="btn btn-outline-success d-block w-50 mt-3"
                href={dictionaryDownload}
                role="button"
              >
                Download
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Gallery;
